.hamburger-lines {
  height: 16px;
  width: 30px;
  z-index: 20;
  flex-direction: column;
  justify-content: space-between;
  display: flex;
  position: absolute;
  top: 16px;
  right: 20px;
}

@media screen and (min-width: 1280px) {
