
.program__page{
  font-family: var(--secondary-font);
  font-weight: 400;
  font-size: 24px;
  line-height: 30px;
  text-align: center;
  margin-bottom: 8px;
  @media screen and (min-width:$tablet) {
      text-align: left; 
  }

  @media screen and (min-width:$desktop) {
    font-size: 36px;
    line-height: 65px;  
    margin-bottom: 20px;
  }
  
}
.program__item{
  font-size: 16px;
  line-height: 1.25;
  &::before{
      content: '>';
  }
 
}

.program__items{
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-content: flex-start;

  margin-top: 55px;
  flex-basis: 350px;
  margin-left: auto;
  margin-right:auto;
  @media screen and (min-width:$tablet) {
      margin-top: 0;
      margin-left: 32px;
      
  }
  @media screen and (min-width:$desktop) {
     flex-basis: 500px;
     margin-left: 142px;
      
  }





}
.program__tumb{
  position: relative;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  flex-direction: column;
  
  width: 130px;
  height: 130px;
  border-radius: 50%;
  
  background-color:var(--background-color)
   ; 
   box-shadow: 0px 4px 4px rgba(171, 129, 108, 0.25);

   @media  screen and (min-width:$tablet) {
       width: 152px;
       height: 152px;
       
   }
   @media  screen and (min-width:$desktop) {
      width: 180px;
      height: 180px;
      
  }

}
.program__title{
  font-family: var(--secondary-font);
  font-weight: 400;
  font-size: 18px;
  line-height: 1.277;
   margin-top: 44px;

   @media  screen and (min-width:$desktop) {
        font-size: 24px;
        line-height: 30px; 
        margin-top: 48px;
      
  }


}
.program__subtitle{
  margin-bottom: 8px;
  @media screen and (min-width:$desktop) {
      font-size: 18px;
      line-height: 22px;
      margin-bottom: 20px;
    }


  
}
.program__text{
  font-size: 14px;
  line-height: 17px;
  @media  screen and (min-width:$desktop) {
     font-size: 16px;
     line-height: 20px;
  }

  
}

.program__item+.program__item{
  margin-top: 8px;
  @media screen and (min-width:$desktop) {
      margin-top: 16px;
    }

}
.program__tumb:nth-child(2){
  margin-top: 36px;
  margin-left: 20px;
  @media screen and (min-width:$tablet) {
      margin-left: 32px;
      margin-top: 33px;
    }
    @media screen and (min-width:$desktop) {
      margin-left: 140px;
      margin-top: 106px;
    
    }

}
.program__tumb:nth-child(3){
  margin-top: 27.5px;
  @media screen and (min-width:$desktop) {
     
      margin-top: 0;
    
    }
}

.program__tumb:nth-child(4){
  margin-top: 59px;
  margin-left: 20px;
  @media screen and (min-width:$tablet) {
      margin-left: 32px;
      margin-top: 45.5px;
    }

    @media screen and (min-width:$desktop) {
      margin-left: 140px;
      margin-top: 75px;
    
    }

}

// read more 

.program__lable{
  color: var(--acent-color);
  font-family: 'Caveat Brush';
  display: block;
  cursor: pointer;
  &:hover{
    color: var(--hover-color);
  }
  &:active{
    color: var(--actived-color);
  }
  position: relative;
 
  
  line-height: 23px;
  margin-top: 16px;
  @media screen and (min-width:$desktop) {
      display: none;
     }
}

.program__checkbox{
  display: none;

}
.program__content{
  display: none;
  margin-top: 8px;
  @media screen and (min-width:$desktop) {
     display: block;
     margin-top: 16px;
    }
}
.program__checkbox:checked ~ .program__content{
  display: block;
}

.program__checkbox:checked ~ .program__lable {
  visibility: hidden;
}

.program__checkbox:checked ~ .program__lable::before {
  content: 'Less';
  display: block;
  position: absolute;
  top:0;
  visibility: visible;
 
 
}



@media screen and (min-width:768px) {
.program__container{
    display: flex;
    
}  
.program__flex{
  display: flex;
  flex-direction: column;
  text-align: left;
  max-width: 336px;
  @media screen and (min-width:$desktop) {
      max-width: 400px;
    }

}
}

